@function cr($size) {
  $remSize: $size / 16px;
  @return #{$remSize}rem;
}
@function ind($i){
  $size: 1px / 3px * $i;
  @return #{$size}rem;
}

$primary: #ff6917;
$secondary: #ff6917;
$rating-star: #ffc917;


//форма поиска на главной
$color-label-search-form: white;
$background-header-form: transparent;
$background-body-form: transparent;
//настройки текста контента
$font-body: Roboto;
$nav-color-link:#263238;
$nav-color-link-mobile:#263238;
$main-color-text: #263238;
$secondary-color-text: #455a64;

//цвет вторичного фона
$section-background-2:#f4f7f7;

//цвета опции тура
$color-best-price: red;
$color-new: #ffc917;
$color-doc: $primary;
$color-custom-tour:green;

//тени
$shadow:0 0px 15px #d8d8d847;
$shadow-hover: 0 0px 15px #77767647;

//бордер
$border-card: 1px solid #b3b3b3 !important;

//карточки
$card-hover-background:#40322d98;
$card-background: white;
//navbar
$navbar-background: white;

//contact--list
$navbar-dectop--contact--list-flexbasis: 65%;
$navbar-contact--list-item-padding:cr(10px);
$navbar-desctop--contact--list-item-padding: cr(3px);
$navbar-desctop--contact--list-font-size: cr(13px);

//цвет линии hr


$hr-color:263238;
.hr{
  height: 1px;
  width: 100%;
  background: $hr-color;
}

//кнопки
$btn-radius:cr(5px);
$theme-colors: (
        "primary": $primary,
        "secondary": $secondary
);
$btn-padding: cr(15px);
$btn-height: cr(45px);
$btn-width: cr(45px);

// форма
$input-radius:cr(5px);
$input-height: cr(45px);


//Заголовки
$font-h: Merriweather;
$header-color: #263238;
$header-color-invers:#fffdfc;
$header-hr-width: cr(100px);
$header-hr-height: cr(2px);
$header-hr-color: #263238;
h1,h2,h3,h4,h5,h6{
  font-family: $font-h;
  color: $header-color;
}

//сетка
$max-width-container:1560px;
$container-max-widths: (
        sm: 660px,
        md: 840px,
        lg: 1080px,
        xl: 1320px
);
$grid-breakpoints: (
        xs: 0,
        sm: 660px,
        md: 840px,
        lg: 1080px,
        xl: 1320px
);


body{
  min-height: 2600px;

  color: $main-color-text;
  font-family: $font-body;
}